<template>
  <div class="register">
		<el-row :gutter="20">
		  <el-col :span="24">
		  	<el-input size="large" placeholder="设置用户名，大于6位的英文或数字" v-model="username">
	  		</el-input>
		  </el-col>
		  <el-col :span="24">
			  <el-input size="large" type="password" placeholder="输入密码" v-model="password">
			  </el-input>
		  </el-col>
		  <el-col :span="24">
			  <el-input size="large" type="password" placeholder="重复密码" v-model="password2"></el-input>
		  </el-col>
		  <el-col :span="24">
		  	<el-input size="large" placeholder="输入常用的邮箱" v-model="email">
		  		<el-button slot="append">提交</el-button>
	  		</el-input>
		  </el-col>
		</el-row>
  </div>
</template>

<script>
export default {
	data () {
		return {
			username: null,	
			password: null,
			password2: null,
			email: null
		}
	},
	methods: {
		
	}
}
</script>

<style lang="less" scoped>
	.register {
		padding: 30px 300px 0;
		.el-col {
			height: 70px;
		}
	}
</style>



